import { Component } from 'react';
import Spin from '../spin';
import Alert from '../../alert';
import Button from '../../button';
import style from './index.css';

export default class SpinDemo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isShow1: false,
      isShow2: false,
    };
  }

  swichHandle1 = () => {
    this.setState({
      isShow1: !this.state.isShow1
    })
  }

  swichHandle2 = () => {
    this.setState({
      isShow2: !this.state.isShow2
    })
  }

  render() {
    return (
      <div className="markdown-block">
        <h3></h3>
        <Spin />
        <h3></h3>
        <Spin size="small" />
        <Spin size="default" />
        <Spin size="large" />
        <h3></h3>
        <Spin tip="loading..." />
        <h3></h3>
        <div className={style.example1}>
          <Spin />
        </div>
        <div className={style.example1}>
          <Spin tip="loading..." />
        </div>
        <h3></h3>
        <div>
          <Spin spinning={this.state.isShow1}>
            <Alert
              type="info"
              message=""
              description="
              
              
              
              "
            />
          </Spin>
          <p>
            <Button type="primary" onClick={this.swichHandle1}>/</Button>
          </p>
        </div>
        <h3></h3>
        <div>
          <Spin spinning={this.state.isShow2} delay={600}>
            <Alert
              type="info"
              message=""
              description="
              
              
              
              
              "
            />
          </Spin>
          <p>
            <Button type="primary" onClick={this.swichHandle2}>/</Button>
          </p>
        </div>
      </div>
    );
  }
}



