import { Component } from 'react';
import moment from 'moment';
import DatePicker from '../index';
import Checkbox from '../../checkbox';

const { MonthPicker, RangePicker } = DatePicker;

export default class DatePickerDemo extends Component {
  state = {
    disabled: false,
    date: moment().add(1, 'M'),
  }
  onChange(m) {
    console.log(m);
  }
  changeDisabled = () => {
    this.setState({
      disabled: !this.state.disabled,
    });
  }
  render() {
    const { date, disabled } = this.state;
    return (
      <div className="markdown-block">
        <Checkbox
          checked={disabled}
          onChange={this.changeDisabled}
        >
          
        </Checkbox>
        <h2></h2>
        <h3></h3>
        <DatePicker
          disabled={disabled}
          onChange={this.onChange}
        />
        <h3></h3>
        <DatePicker
          disabled={disabled}
          value={date}
          onChange={(d) => {
            this.setState({
              date: d,
            });
          }}
        />
        <p>: {date.format()}</p>
        <h2></h2>
        <p> disabledDate </p>
        <DatePicker
          disabled={disabled}
          disabledDate={(current) => {
            return current && current.valueOf() < Date.now();
          }}
        ></DatePicker>
        <h2></h2>
        <MonthPicker onChange={this.onChange} disabled={disabled} />
        <h2></h2>
        <p>RangePicker   </p>
        <RangePicker onChange={this.onChange} disabled={disabled} />
      </div>
    );
  }
}



